<template>
  <div class="login d-flex flex-column align-items-center">
    <h2 class="animated fadeInDown">Your <span class="text-primary">ToDoList</span></h2>
    <img class="animated zoomIn mb-3" :src="logo" alt="">
    <div class="animated fadeInUp w-100">
      <form>
        <div class="form-group ">
          <input type="email" v-model="userInfoBySign.account" class="form-control" aria-describedby="emailHelp"
            placeholder="Account">
        </div>
        <div class="form-group">
          <input type="password" v-model="userInfoBySign.password" class="form-control" placeholder="Password">
        </div>
        <button v-if="toggleSignForm" type="submit" class="btn btn-primary mb-3" @click.prevent="signIn">Sign In</button>
        <button v-else type="submit" class="btn btn-success mb-3" @click.prevent="signUp">Sign Up</button>
        <br>
        <div v-if="toggleSignForm" class="sign-up-wrap">Create Account,
          <span class="text-success sign-up" @click="toggleBlock">Sign Up</span>
        </div>
        <div v-else class="sign-up-wrap">
          <span class="text-primary sign-up" @click="toggleBlock">Back Sign In</span>
        </div>
      </form>
      <hr>
      <button class="btn btn-outline-primary w-100" @click="login('google')">
        <font-awesome-icon fixed-width class="ml-auto text-primary" size="lg" :icon="['fab', 'google']" />
        Use Google Account Sign In
      </button>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
import logo from '../../static/logo.png'

import { rtdbDBAuth, rtdbDB } from '@/service/rtdb_db'
export default {
  data () {
    return {
      toggleSignForm: true,
      userInfoBySign: {
        account: '',
        password: ''
      },
      accountListByDB: [],
      logo: logo,
      userInfoByDB: null,
      isAuth: false // Is authorized flag
    }
  },
  methods: {
    login (providerName) {
      let provider = null
      switch (providerName) {
        case 'google':
          provider = new firebase.auth.GoogleAuthProvider()
          break
      }

      rtdbDBAuth.signInWithPopup(provider)
        .then(result => {
          this.userInfoByDB = result.user
          let { displayName, email, emailVerified } = result.user
          return { displayName, email, emailVerified }
        }).then(user => {
          this.$store.commit('setLoginType', 'app')
          console.log('[user]', user)
          this.$store.commit('setUserInfo', user)
          this.$router.push('/')
        }).catch(err => this.$swalErrorHandle(err))
    },
    validate () {
      const { account, password } = this.userInfoBySign
      const userDBRef = this.userInfoBySign.account
      let passwordByDB = ''

      if (!account) throw Error('帳號必填')
      if (!account.match(this.$accountRegex)) throw Error('帳號格式錯誤。須含英文及數字，開頭是英文')
      if (!password) throw Error('密碼必填')

      if (this.toggleSignForm) {
        // sign in（登入）
        const isAccount = this.accountListByDB.find(item => {
          return item === this.userInfoBySign.account
        })

        rtdbDB.ref(`${userDBRef}/password`).on('value', snapshot => {
          passwordByDB = snapshot.val()
        })
        const isSuccess = password === passwordByDB

        if (!isAccount) throw Error('糟糕，查無此帳號')
        if (!isSuccess) throw Error('密碼錯誤')
      } else {
        // sign up（註冊）
        const duplicateUser = this.accountListByDB.find(item => item === account)
        console.log('[duplicateUser]', duplicateUser)
        if (duplicateUser) throw Error('糟糕，此帳號已存在')
      }
      return true
    },
    signIn () {
      try {
        this.validate()

        console.log('[userInfoBySign]', this.userInfoBySign)
        this.$store.commit('setLoginType', 'default')
        this.$store.commit('setUserInfo', this.userInfoBySign)
        this.$messageHandle('success', '登入成功！')

        this.$router.push('/')
      } catch (e) {
        this.$swalErrorHandle(e.message)
      }
    },
    signUp () {
      try {
        this.validate()

        const userDBRef = this.userInfoBySign.account
        console.log('[userDBRef]', userDBRef)
        this.$store.commit('setLoginType', 'default')

        rtdbDB.ref(`${userDBRef}`).set({
          password: this.userInfoBySign.password
        })

        rtdbDB.ref(`${userDBRef}/todoList`).on('value', snapshot => {
          const data = snapshot.val()
          const dict = Object.assign({}, this.todo)
          dict.name = 'default'
          dict.isShow = false

          if (data) {
            this.todoGroupList = data
            // this.isLoading = true
          } else {
            rtdbDB.ref(`${userDBRef}/todoList/today`).push(dict)
          }
          console.log('[userInfoBySign]', this.userInfoBySign)
          this.$store.commit('setUserInfo', this.userInfoBySign)
          this.$router.push('/')
        })
      } catch (e) {
        this.$swalErrorHandle(e.message)
      }
    },
    toggleBlock () {
      this.toggleSignForm = !this.toggleSignForm
      this.userInfoBySign.account = ''
      this.userInfoBySign.password = ''
    }
  },
  created () {
    rtdbDB.ref('/').on('value', snapshot => {
      const data = snapshot.val()
      this.accountListByDB = Object.keys(data)
      console.log('[accountListByDB]', this.accountListByDB)
    })
  },
  beforeCreate () {
    rtdbDBAuth.onAuthStateChanged((user) => {
      if (user) {
        this.userInfoByDB = user
        this.isAuth = true
      }
    })
  }
}
</script>

<style>
</style>



